<template>

  <div>
    <el-input class="c_room_input" v-model="roomId" placeholder="请输入房间号"></el-input>
    <el-button type="primary" @click="onCreatClick">create</el-button>
    <el-button type="success" @click = 'onJoinClick'>join</el-button>
    <div>
      <video id="c_video_local"></video>
      <div id="remoteVideos"></div>
    </div>

  </div>
</template>

<script>

  import SimpleWebRTC from 'simplewebrtc';
  export default {
    name: "Home",
    mounted(){

    },
    data() {
      return {
        roomId: '',
        webrtc:null,
      }
    },
    methods:{

      initWebRtc(){
        this.webrtc = new SimpleWebRTC({
          // the id/element dom element that will hold "our" video
          localVideoEl: 'c_video_local',
          // the id/element dom element that will hold remote videos
          remoteVideosEl: 'remoteVideos',
          // immediately ask for camera access
          autoRequestMedia: true,
          debug:true
        });
      },
      onCreatClick(){

        if (this.webrtc == null){
          this.initWebRtc();
        }

        this.webrtc.createRoom(this.roomId,(a) => {
          console.log(a);
        })

      },
      onJoinClick(){

        if (this.webrtc == null){
          this.initWebRtc();
        }
        this.webrtc.joinRoom(this.roomId,(a) => {
          console.log(a);
        })

      }
    },
  }
</script>

<style scoped>

  .c_room_input {

    width: 200px;
  }

</style>



